/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f6f9;
  color: #333;
}

/* Sidebar Styles */
.admin-dashboard {
  display: flex;
  height: 100vh;
  /* Ensure the layout fills the screen */
}

.sidebar {
  width: 250px;
  height: 100vh;
  background-color: white;
  color: black;
  padding: 20px;
  margin-top: 85px;
  /* Adjusted to allow space for header */
  top: 0;
  left: 0;
  overflow-y: auto;
  /* Makes sidebar scrollable */
  position: fixed;
  /* Keep the sidebar fixed on larger screens */
}

.sidebar .text-2xl {
  font-size: 24px;
  font-weight: bold;
  color: black;
}

.sidebar .nav-links {
  list-style: none;
  padding: 0;
}

.sidebar .nav-links li {
  margin: 20px 0;
  cursor: pointer;
  padding: 10px;
  border-radius: 5px;
}

.sidebar .nav-links li:hover,
.sidebar .nav-links li.active {
  background-color: #FFC0CB;
  color: black;
  transition: background-color 0.3s ease;
}

/* Content Styles */
.content {
  margin-left: 250px;
  /* To make space for the sidebar */
  padding: 20px;
  flex: 1;
  height: 100vh;
  overflow-y: auto;
  /* Allows scrolling of content */
  margin-top: 85px;
  /* Adjusted for header space */
}

h3 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 15px;
}

/* Table Styles */
.table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.table th,
.table td {
  padding: 10px;
  text-align: left;
  border: 1px solid #ddd;
}

.table th {
  background-color: #f4f6f9;
}

.table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.table .btn {
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin: 5px;
}

.table .btn.edit {
  background-color: #4958e2;
  color: #fff;
}

.table .btn.delete {
  background-color: #e74c3c;
  color: #fff;
}

/* Responsive Styles */
@media (max-width: 1024px) {
  .sidebar {
    width: 200px;
  }

  .content {
    margin-left: 200px;
  }
}

@media (max-width: 768px) {
  .sidebar {
    width: 100%;
    position: relative;
    /* Sidebar becomes relative on small screens */
    height: auto;
    /* Sidebar no longer takes full height */
  }

  .content {
    margin-left: 0;
    /* Remove space for sidebar */
    padding: 10px;
  }

  .admin-dashboard {
    flex-direction: column;
    /* Stack the sidebar and content vertically */
  }

  .sidebar .nav-links li {
    font-size: 14px;
    /* Smaller font for mobile screens */
    padding: 8px;
  }

  .table th,
  .table td {
    padding: 8px;
    /* Adjust padding in tables */
  }
}

@media (max-width: 480px) {
  .sidebar .nav-links li {
    font-size: 12px;
    /* Even smaller font size for smaller screens */
    padding: 6px;
  }

  .table th,
  .table td {
    padding: 6px;
    /* Smaller padding for tables on very small screens */
  }

  h3 {
    font-size: 20px;
    /* Adjust heading size */
  }

  .table .btn {
    padding: 4px 8px;
    /* Smaller buttons for small screens */
  }
}