/* About.css */

/* Background and container styling */
.about {
    background-color: #f7fafc;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
  }
  
  /* Header styling */
  h1 {
    font-size: 2.5rem;
    /* color: #ec4899; */
    font-weight: bold;
  }
  
  /* Flex layout for bio and image */
  .flex {
    display: flex;
  }
  
  .lg\\:flex-row {
    flex-direction: row;
  }
  
  .lg\\:text-left {
    text-align: left;
  }
  
  .text-center {
    text-align: center;
  }
  
  /* Profile image */
  .w-full {
    width: 100%;
  }
  
  /* .w-1\\/3 {
    width: 33.33%;
  } */
  
  /* .w-2\\/3 {
    width: 66.66%;
  } */
  
  .rounded-lg {
    border-radius: 8px;
  }
  
  .overflow-hidden {
    overflow: hidden;
  }
  
  .shadow-lg {
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  }
  
  /* Text and content styling */
  .text-xl {
    font-size: 1.25rem;
  }
  
  .text-lg {
    font-size: 1.125rem;
  }
  
  .text-gray-700 {
    color: #4a5568;
  }
  
  .text-gray-600 {
    color: #718096;
  }
  
  .text-pink-500 {
    color: #ec4899;
  }
  
  .list-disc {
    list-style-type: disc;
  }
  
  .pl-6 {
    padding-left: 1.5rem;
  }
  
  .mt-6 {
    margin-top: 1.5rem;
  }
  
  .mb-12 {
    margin-bottom: 3rem;
  }
  
  .mb-6 {
    margin-bottom: 1.5rem;
  }
  
  .mb-4 {
    margin-bottom: 1rem;
  }
  
