.portfolio {
    padding: 20px;
    text-align: center;
  }
  
  .gallery {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
    margin-top: 20px;
  }
  
  .gallery-item img {
    width: 100%;
    height: auto;
    border-radius: 8px;
  }
  
  .gallery-item p {
    margin-top: 10px;
    /* color: #555; */
  }


  /* Portfolio.css */

.portfolio {
  background-color: #f7fafc;
}

.gallery-item {
  position: relative;
}

.gallery-item img {
  width: 100%;
  height: 50rem; /* Custom height */
  object-fit: cover;
  transition: transform 0.3s ease-in-out;
}

.gallery-item .overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.gallery-item:hover .overlay {
  opacity: 1;
}

.gallery-item p {
  position: absolute;
  bottom: 15px;
  left: 15px;
  font-size: 1.2rem;
  font-weight: bold;
}

  