/* Footer.css */

/* General footer styling */
.footer {
    /* background-color: #333; */
    /* color: white; */
    padding: 1rem;
    text-align: center;
    position: relative;
    width: 100%;
    bottom: 0;
  }
  
  .footer-text p {
    font-size: 0.875rem; /* Slightly smaller text */
    margin: 0;
    opacity: 0.7;
  }
  
  /* Hover effect to make the footer text a bit brighter */
  .footer-text p:hover {
    opacity: 1;
    transition: opacity 0.3s;
  }
  
  /* Responsive design */
  @media (max-width: 500px) {
    .footer-text p {
      font-size: 0.75rem; /* Slightly smaller font on smaller screens */
    }
  }
  