@keyframes gradientChange {
    0% {
      background: linear-gradient(45deg, #a1c4fd, #c2e9fb); /* Soft blue to light sky blue */
    }
    25% {
      background: linear-gradient(45deg, #ff9a9e, #fad0c4); /* Light pink to peach */
    }
    50% {
      background: linear-gradient(45deg, #d4fc79, #96e6a1); /* Soft green to light mint */
    }
    75% {
      background: linear-gradient(45deg, #fbc2eb, #a6c1ee); /* Light lavender to soft blue */
    }
    100% {
      background: linear-gradient(45deg, #a1c4fd, #c2e9fb); /* Soft blue to light sky blue */
    }
  }
  
  .bg-gradient-change {
    animation: gradientChange 15s ease infinite; /* Slower transition for a smooth effect */
  }
  