  .booking-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 30px;
    background-color: rgba(255, 255, 255, 0.9); /* Slight transparency for card effect */
    border-radius: 16px; /* Rounded corners */
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.1); /* Card-like shadow effect */
    font-family: 'Roboto', sans-serif;
    transition: box-shadow 0.3s ease, transform 0.3s ease; /* Smooth hover transition */
  }
  
  .booking-container:hover {
    transform: translateY(-5px); /* Slight lift effect on hover */
    box-shadow: 0 18px 36px rgba(0, 0, 0, 0.2); /* Darker shadow on hover */
  }
  
  .booking-title {
    text-align: center;
    font-size: 32px;
    font-weight: bold;
    color: #2c3e50;
    margin-bottom: 10px;
  }
  
  .booking-subtitle {
    text-align: center;
    font-size: 16px;
    color: #7f8c8d;
    margin-bottom: 30px;
  }
  
  .booking-form {
    display: flex;
    flex-direction: column;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  label {
    font-size: 16px;
    margin-bottom: 8px;
    color: #34495e;
    display: block;
  }
  
  input,
  select {
    width: 100%;
    padding: 14px;
    margin-top: 8px;
    border: 1px solid #bdc3c7;
    border-radius: 8px;
    font-size: 14px;
    color: #34495e;
  }
  
  input[type="text"],
  input[type="email"] {
    padding: 14px;
  }
  
  button {
    padding: 15px;
    /* background-color: red; */
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: 20px;
  }
  
  button:hover {
    /* background-color: #e74c3c; Slightly darker pink for hover */
  }
  
  button:active {
    /* background-color: #c0392b; Darker red for active */
  }
  
  /* Responsive Styles */
  @media (max-width: 768px) {
    .booking-container {
      margin: 20px;
      padding: 20px;
    }
  
    .booking-title {
      font-size: 28px;
    }
  
    .form-group label {
      font-size: 14px;
    }
  
    input,
    select {
      font-size: 14px;
      padding: 12px;
    }
  
    button {
      font-size: 14px;
      padding: 12px;
    }
  }
  